.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    text-align: center;
    transition: background 0.5s ease;
}

.weather-container {
    background: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

input {
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
}

button:disabled {
    background-color: #ccc;
}

.default-sky {
    background: linear-gradient(to bottom, #87CEEB, #f0f8ff);
}

.sunny {
    background: linear-gradient(to bottom, #FFD700, #FFA500);
}

.rainy {
    background: linear-gradient(to bottom, #A9A9A9, #808080);
}

.cloudy {
    background: linear-gradient(to bottom, #D3D3D3, #A9A9A9);
}

.cloud {
    width: 100px;
    height: 60px;
    background: white;
    border-radius: 50%;
    position: absolute;
    top: 20%;
    animation: moveClouds 20s linear infinite;
}

@keyframes moveClouds {
    0% {
        left: -150px;
    }
    100% {
        left: 100%;
    }
}
